import React, { Component } from 'react';
import { saveSvgAsPng } from 'save-svg-as-png';
import UIkit from 'uikit';

let svgDataLock = 0; // This stops svgOG from being updated the first execution.
let svgOG = [];

class Corrupt extends Component {
  constructor(props) {
    super(props);
    this.state = {
      corruptAmount: 5, // Default corruption multiplier
      corruptReplace: 0, // Default corruption target values
      corruptUseMultiplier: true, //Default to use corruptAmount as a multiplier for a random value
    };
  }

   setCorruptAmount = (e) => {
    this.setState({
      corruptAmount: e.target.value
    });
  }

  setCorruptReplace = (e) => {
    this.setState({
      corruptReplace: e.target.value
    });
  }

  setCorruptUseMultiplier = (e) => {
    this.setState({
      corruptUseMultiplier: !this.state.corruptUseMultiplier
    });
  }

  svgReset = (attr = "") => {
    let svgPaths = document.querySelectorAll("svg path");
    for (let i = 0; i < svgPaths.length; i++) {
      svgPaths[i].setAttribute('d', svgOG[i].getAttribute('d'));
    }
  }

  setCorruptValue = () => {
    if (this.state.corruptUseMultiplier) {
      return Math.floor(Math.random() * this.state.corruptAmount);
    }
    return this.state.corruptAmount;
  }

  setSvgUrl = (e) => {
    this.props.url(e.target.value);
    this.svgReset;
  }

  getSvgPng = () => {
    let download = document.querySelector("#scene > svg");
    let scale = 1
    if (download.attributes.viewBox.value === "0 0 36 36") {
      saveSvgAsPng(download, "corrupted.png", {scale: 30});
    } else {
      saveSvgAsPng(download, "corrupted.png");
    }

  }

  svgCorrupt = () => {
    let corruptTarget = new RegExp(`[${this.state.corruptReplace}]`, 'gi');
    let svgPaths = document.querySelectorAll("svg path");
    if (svgDataLock === 0) {
    svgPaths.forEach(function(e, idx){
    svgOG.push(svgPaths[idx].cloneNode(true));});}
    for (let i = 0; i < svgPaths.length; i++) {
      svgPaths[i].setAttribute('d', svgOG[i].getAttribute('d'));
      svgPaths[i].setAttribute('d', svgPaths[i].getAttribute('d')
        .replace(corruptTarget, this.setCorruptValue));
      }
      svgDataLock = 1;
      console.log("corrupting targets " + this.state.corruptReplace);
      console.log("replacing with " + this.state.corruptAmount);
    };


  render() {
    return (
      <React.Fragment>
      <label className="uk-form-label" for="url">Load SVG from URL</label>
      <input
        className={"uk-input"}
        placeholder="SVG URL"
        name="url"
        type="text"
        onChange={this.setSvgUrl}
        defaultValue=""
      />

      <br></br>
      <label className="uk-form-label" for="value" uk-tooltip="title: Searches for each number in this field to target, 789 means 7, 8 and 9 will be targeted.; pos: left">Target values &#10067;</label>
      <input
        className={"uk-input"}
        type="number"
        name="value"
        onChange={this.setCorruptReplace}
        placeholder="789"
      />

      <br></br>
      <label className="uk-form-label" for="quantity">Replace with</label>
      <input
        className="uk-range"
        type="range"
        name="quantity"
        min="1"
        max="100"
        onChange={this.setCorruptAmount}
        //defaultValue={this.state.corruptAmount}
        value={this.state.corruptAmount}
      />
      {this.state.corruptAmount}
      <output for="quantity">
      </output>


      <label uk-tooltip="title: If checked will use the above value as a multiple for a random value.; pos: left">
      <input
        className="uk-checkbox"
        type="checkbox"
        checked={this.state.corruptUseMultiplier}
        onChange={this.setCorruptUseMultiplier}
        //defaultValue={this.state.corruptUseMultiplier}

      />
      Use Multiplier &#10067;
      </label>

      <br></br>
      <button onClick={this.svgReset} className="uk-button uk-button-primary" >Reset</button>
      <button onClick={this.svgCorrupt} className="uk-button uk-button-danger">CORRUPT!</button>


      <button onClick={this.getSvgPng} className="uk-button uk-button-default" style={{color: "white"}}>download</button>
      </React.Fragment>
    );
  }
}

export default Corrupt;
