//import React from 'react';
import ReactDOM from 'react-dom';
import React, { Component } from 'react';
import Canvas from './components/canvas.jsx';
import '../assets/stylesheets/application.scss';
import Corrupt from './components/corrupt.jsx';
import Emojis from './components/emoji.jsx';

import panzoom from 'panzoom';

const root = document.getElementById('root');

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      svgLink: "https://upload.wikimedia.org/wikipedia/commons/f/fd/Ghostscript_Tiger.svg",
    };
  }

  setSvgLink = (Url) => {
      this.setState({svgLink: Url})
  }

  setEmojiLink = (Url) => {
      this.setState({svgLink: Url})
  }

  ignorePanzoom = (e) => {
    let element = document.getElementById('scene');
    let controller = panzoom(element);
    controller.pause();
  }

  startPanzoom = (e) => {
    let element = document.getElementById('scene');
    let controller = panzoom(element);
    controller.resume();
  }


  render () {
  return (
    <React.Fragment>
    <div id="controls-panel" >
      <div id="controls">
        <Corrupt url={this.setSvgLink} />
        <Emojis emoji={this.setSvgLink} />
        <br></br>
        <a href="https://github.com/x8BitRain/svg-emoji-corrupt" className="href">GitHub</a>
      </div>
    </div>
    <div id="outer-scene" onMouseLeave={this.ignorePanzoom} onMouseEnter={this.startPanzoom}>
      <div id="scene">
      <Canvas svgUrl={this.state.svgLink}/>
      </div>
    </div>
    </React.Fragment>
   );
  }
};

if (root) {
  ReactDOM.render(<App />, root);
}

