import React, {Component} from 'react';
import { Picker } from 'emoji-mart'

class Emojis extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: ""
    };
  }

  handleUpdate = (e) => {
    this.props.emoji("https://twemoji.maxcdn.com/2/svg/" + e.unified + ".svg");
    console.log("https://twemoji.maxcdn.com/2/svg/" + e.unified + ".svg");
   }


  render() {
        return (
            <Picker set='twitter'
            onSelect={this.handleUpdate}
            style={{ width: '100%', marginTop: "10px" }}
            showPreview={false}
            />
        );
    }
}

export default Emojis;


